.vetskitchen-buttons-with-image {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  grid-gap: 30px;
  padding: 30px 0;
}
.vetskitchen-buttons-with-image a {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  padding: 10px;
  width: 100%;
  max-width: calc(50% - 15px);
  color: #ffffff;
  background-color: #008077;
  border-radius: 20px;
}
.vetskitchen-buttons-with-image a .text-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 26px;
}
.vetskitchen-buttons-with-image a .text-wrapper svg {
  max-width: 10px;
}
.vetskitchen-buttons-with-image a .text {
  position: relative;
  font-size: 26px;
  font-weight: bold;
}
.vetskitchen-buttons-with-image a .text:after {
  content: "";
  position: absolute;
  bottom: -16px;
  left: -8px;
  width: 144px;
  height: 10px;
  background-image: url("../../assets/images/border__image.png");
  background-repeat: no-repeat;
  background-size: contain;
}
.vetskitchen-buttons-with-image a > img {
  max-width: 100px;
  height: auto;
}
.vetskitchen-buttons-with-image a:hover {
  color: #ffffff;
  text-decoration: none;
}
@media screen and (max-width: 767px) {
  .vetskitchen-buttons-with-image a .text-wrapper {
    grid-gap: 10px;
  }
}
@media screen and (max-width: 480px) {
  .vetskitchen-buttons-with-image {
    grid-gap: 10px;
  }
  .vetskitchen-buttons-with-image a {
    max-width: calc(50% - 5px);
  }
  .vetskitchen-buttons-with-image a .text-wrapper svg {
    max-width: 6px;
  }
  .vetskitchen-buttons-with-image a .text {
    font-size: 16px;
  }
  .vetskitchen-buttons-with-image a .text:after {
    bottom: -11px;
    width: 100%;
  }
  .vetskitchen-buttons-with-image a > img {
    max-width: 50px;
  }
}